.requestModal {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 700;
  height: 580;
  background-color: white;
  border-radius: 10px;
}
.bor {
